/* global rules start  */
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital@1&display=swap');

:root {
    --main-color: silver;
    --main-bgColor: #f6f6f6;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    /* font-family: 'Work Sans', sans-serif; */
    font-family: 'Josefin Sans', sans-serif;

}

html {
    scroll-behavior: smooth;
}

.container {
    margin-right: auto;
    margin-left: auto;
    padding-left: 15px;
    padding-right: 15px;
}

/* media query start */

/* very small screens  */
@media (min-width: 400px) {
    .container {
        width: 500px;
    }
}

/* small screens*/
@media (min-width: 768px) {
    .container {
        width: 740px;
    }
}

/* medium screens  */
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}

/* large screens  */
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}

/*media query end*/

/* Start Components */
.special-heading {
    color: #ebeced;
    font-size: 100px;
    text-align: center;
    letter-spacing: -3px;
}

.special-heading+p {
    margin: -30px 0 0;
    font-size: 20px;
    text-align: center;
    color: #797979;
}

@media (max-width: 767px) {
    .special-heading {
        font-size: 60px;
    }

    .special-heading+p {
        margin-top: -20px;
    }
}

/* End Components */
/* global rules end  */

/* header start */
header {
    padding: 15px;
}

header .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid var(--main-color);
}

header .container img {
    width: 40px;
}

header .container ul {
    display: flex;
    align-items: center;
}

header .container ul li {
    list-style: none;
}

header .container ul li a {
    color: black;
    text-decoration: none;
    cursor: pointer;
    padding: 6px;
    text-transform: capitalize;
}

header .container ul li a:hover {
    color: var(--main-color);
}

/* header end */

/* main start */
main {
    background-image: url('img/2.jpg');
    background-size: cover;
    height: calc(100vh - 70px);
    position: relative;
}

main .middle-text {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    width: 320px;
    max-width: 100%;
}

main .middle-text h1 {
    color: var(--main-color);
    font-size: 50px;
    border: 1px solid var(--main-color);
    padding: 3px;
}

main .middle-text p {
    font-size: 18px;
    line-height: 1.8;
    margin: 19px 0;
}

/* main end */

/* aside start */
aside {
    background-color: var(--main-bgColor);
    padding: 60px 0 60px 0;
}

aside .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
}

aside .container .feature {
    padding: 20px;
    text-align: center;
}

aside .feature i {
    font-size: 48px;
    color: var(--main-color);
}

aside .feature h1 {
    margin: 30px 0 30px 0;
    font-size: 19px;
    border: 1px solid var(--main-color);
    padding: 3px;
}

aside .feature p {
    line-height: 1.8;
    color: #777777;
}

/* aside end */

/* services start */
.services {
    padding: 60px 0;
}

.services .container .our-services {
    margin: 100px 0 0 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 30px;
}

.services .container .our-services .col .srv {
    display: flex;
    margin-bottom: 40px;
}

@media (max-width: 767px) {
    .services .container .our-services .col .srv {
        flex-direction: column;
        text-align: center;
    }
}

.services .container .our-services .col .srv .text {
    flex: 1;
}

.services .container .our-services .col .srv i {
    color: var(--main-color);
    flex-basis: 60px;
    font-size: 33px;
}

.services .container .our-services .col .srv .text h3 {
    margin-bottom: 20px;
    border: 1px solid var(--main-color);
    padding: 3px;
}

.services .container .our-services .col .srv .text p {
    margin: 16px 0;
    line-height: 1.7;
    color: #504f4f;
    font-size: 17px;
}

@media (max-width: 992px) {
    .services .container .our-services .col .srv .text p {
        margin: 16px 0;
        line-height: 1.7;
        color: #504f4f;
        font-size: 16px;
    }
}

.services .col .image {
    position: relative;
    text-align: center;
}

.services .col .image::before {
    position: absolute;
    content: "";
    height: calc(100% + 100px);
    width: 70px;
    background-color: var(--main-color);
    right: 10px;
    top: -50px;
    z-index: -1;
}

.services .col .image img {
    width: 260px;
}

@media (max-width: 1200px) {
    .services .col .image {
        display: none;
    }
}

/* services end */

/* portfolio start */
.portfolio {
    background-color: var(--main-bgColor);
    padding: 60px 0;
}

.our-portfolio {
    margin: 80px 0 0 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 30px;
}

.portfolio .our-portfolio .card {
    background-color: white;
}

.portfolio .our-portfolio .card img {
    max-width: 100%;
}

.portfolio .our-portfolio .card .info {
    padding: 20px;
}

.portfolio .our-portfolio .card .info h3 {
    border: 1px solid var(--main-color);
    padding: 3px;
}

.portfolio .our-portfolio .card .info p {
    margin: 16px 0;
    line-height: 1.5;
    color: #777;
    font-weight: 300;
}

/* portfolio end */

/* about start */
.about {
    padding: 60px 0 120px 0;
}

.about .container .about-us {
    margin: 100px 0 0 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.about .about-us .image {
    position: relative;
    text-align: center;
    margin-left: 30px;
}

.about .about-us .image::before {
    position: absolute;
    content: "";
    height: calc(100% + 100px);
    width: 70px;
    background-color: var(--main-color);
    left: -20px;
    top: -50px;
    z-index: -1;
}

.about .about-us .image img {
    width: 260px;
}

.about .about-us .text {
    flex-basis: calc(100% - 500px);
}

.about .about-us .text #first {
    margin-top: 16px;
    margin-bottom: 50px;
    font-weight: bold;
    line-height: 1.8;
}

.about .about-us .text hr {
    border: var(--main-color) inset 1px;
    width: 50%;
    margin: 8px 0;
}

.about .about-us .text #second {
    margin-top: 16px;
    margin-bottom: 16px;
    color: #777777;
    line-height: 1.8;
}

@media (max-width: 950px) {
    .about .container .about-us {
        flex-direction: column;
        text-align: center;
    }

    .about .about-us .image::before {
        display: none;
    }
}

/* about end */

/* contact start */
.contact {
    background-color: var(--main-bgColor);
    padding: 60px 0;
}

.contact .info {
    padding: 60px 0;
    text-align: center;
}

.contact .info p {
    margin: 35px 0 15px 0;
    font-size: 35px;
}

.contact .info a {
    color: var(--main-color);
    font-size: 35px;
}

.contact .info .social {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    border: 1px solid var(--main-color);
    padding: 3px;
}

.contact .info .social .media {
    margin-left: 10px;
}

.contact .info .social .media i {
    margin-left: 6px;
    cursor: pointer;
}

@media (max-width: 767px) {

    .contact .info p,
    .contact .info a {
        font-size: 25px;
    }

}

/* contact end */

/* footer start */
footer {
    padding: 30px 10px;
    background-color: var(--main-color);
}

footer h1 {
    font-size: 20px;
    text-align: center;
}

footer h1 a {
    color: #868585;
    text-decoration: none;
}

@media (max-width: 400px) {
    footer h1 {
        font-size: 17px;
    }
}

/* footer end */